.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.carousel {
  width: 1000px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.carousel-control-next-icon,
.carousel-control-prev-icon {
  filter: invert(1);
}
/* This is not responsive, should be changed later */
.registrationForm {
  margin-left: 50px;
  margin-right: 50px;
}

.profileModForm {
  display: flex;
  justify-content: center;
  align-items: center;
}

.gridPastTournaments {
  position: relative;
  left: 100px;
}

.leftRegistration {
  position: absolute;
  left: 50px;
  padding: 0px;
}

.footer{
  position:absolute;
  left:0;
  bottom:0;
  right:0;
  height: 2.5rem; 
}

.page-container {
  position: relative;
  min-height: 80vh;
}

.main-footer {
  padding-top: 3rem;
  position: relative;
  bottom: 0;
  width: 100%;
}

.card-img-top {
  width: 100%;
  height: 15vw;
  object-fit: cover;
}

.card-txt-top {
  width: 100%;
  height: 4vw;
  object-fit: cover;
}

.youtubeVideo {
  width: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  
}

.logoT {
  width: 70%;
}

.logoList {
  overflow-y: scroll;
  max-height: 37.5rem;
}



.textField{
  height: 32px;
  width: 200px;
  padding: 0 32px 0 16px;
  
  }
.textField:hover {
  cursor: pointer;
}
  
.btnBuscar{
  height: 34px;
  width: 32px;
  text-align: center;
  align-items: center;
  justify-content: center;
}
  
.barraBusqueda{
  float: right;
  padding: 20px;
}

.columna {
  float: left;
  width: 25%;
  padding-right: 5px;
}
    
.fila::after {
  content: "";
  clear: both;
  display: table;
}
img {
      /* Para que sea responsiva */
  width: 100%;
}

.logoGrid {
  margin-bottom: 5rem;
}

.leftLabel {
  margin-left: 15px;
}

